.hero{
  color: white;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  text-align: center;
}
.Profile-image-container{
  margin: 50px auto;
}
.Profile-image{
  width: 120px;
  margin: 20px auto;
  border-radius: 150px;
}
.hero-content p{
  font-size: 13px;
  font-weight: 300;
}
.hero-content a{
  text-decoration: none;
  color: white;
  font-weight: 600;
}
.hero-content a:hover{  
  text-decoration: underline;
}
